.favorite {
  background-color: $backgroundColor;

  @include e(featured) {
    text-align: center;
    font-family: $allura;
    margin: 0;
    font-size: 40px;
    color: $alluraColor;
  }
  @include e(title) {
    font-size: 24px;
    color: $blackBrown;
    text-align: center;
    margin-top: 0;
  }
  @include e(lorem) {
    font-family: $karla;
    text-align: center;
    padding: 0 10px;
    line-height: 1.5;
    color: $lightgrey;
    margin-bottom: 20px;
  }
}
.buttons {
  display: flex;
  justify-content: space-around;
}
.catalog__button.active::after,
.catalog__button:hover::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 1px;
  background-color: $black;
}
.catalog {
  position: relative;

  @include e(button) {
    display: inline-block;
    padding: 10px 20px;
    font-size: 18px;
    color: $blackBrown;
    font-family: $baseFontFamily;
    cursor: pointer;
    position: relative;
    margin-right: 10px;
    margin-bottom: 50px;
  }
  @include e(opacity) {
    opacity: 0.7;
  }
  @include e(favorite) {
    position: absolute;
    background-color: $darkgreen;
    color: $white;
    padding: 10px 20px;
    top: -15px;
    right: 20px;
    font-size: 24px;
  }
  @include e(item) {
    margin: 0;
  }
  @include e(img) {
    margin-bottom: 30px;
    @include m(first) {
      height: 444px;
    }
    @include m(second) {
      height: 370px;
    }
    @include m(third) {
      height: 600px;
    }
    @include m(fourth) {
      height: 210px;
    }
  }
  @include e(descr) {
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    color: $alluraColor;
    margin-bottom: 40px;
  }
  @include e(btn) {
    line-height: 21px;
    font-family: $karla;
    text-transform: uppercase;
    padding: 20px 32px;
    background-color: $blackBrown;
    color: $backgroundColor;
    margin-bottom: 80px;
    display: inline-block;
    font-size: 15px;
    @include m(text) {
      color: $backgroundColor;
      text-decoration: none;
    }
  }
}
.btn-container {
  text-align: center;
}
