.service-bg {
  background: url(../../../i/service_bg.jpg), no-repeat center;

  background-size: cover;
  background-position: 20%;
  min-height: 400px;
  padding-bottom: 50px;
  // display: flex;
  // align-items: center;
  @include e(div) {
    position: relative;
    top: 170px;
  }
  @include e(text) {
    margin: 0;
    font-size: 24px;
    color: $white;
  }
}
.service {
  padding-top: 50px;
  background-color: $backgroundColor;

  @include e(item) {
    background-color: $darkgreen;
    padding: 30px 50px;
    display: flex;
    text-align: center;

    margin-bottom: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  @include e(btn) {
    @include m(text) {
      color: $white;
      font-family: $karla;
      font-size: 16px;
      text-decoration: none;
    }
    &::after {
      content: "\2192";
      width: 10px;
      height: 10px;
      color: $white;
      padding-left: 10px;
    }
  }
  @include e(title) {
    font-size: 24px;
    margin: 0;
    color: $alluraColor;
    margin-bottom: 16px;
  }
  @include e(lorem) {
    color: $white;
    font-weight: 300;
    margin: 0;
    padding: 0 20px;
    font-family: $karla;
    line-height: 24px;
    margin-bottom: 15px;
  }
}
.icon-event {
  fill: $alluraColor;
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
}
