.form {
  background: url(../../../i/background-form.jpg), no-repeat center;
  background-size: cover;
  background-position: 50% 40%;
  text-align: center;
  padding-bottom: 30px;
  max-height: 470px;
  @include e(content) {
    padding: 120px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  @include e(group) {
    width: 80%;
  }
  @include e(title) {
    font-size: 24px;
    line-height: 24px;
    color: $white;
  }

  @include e(place) {
    margin-bottom: 20px;
    padding: 15px 20px;
    font-family: $karla;
    background-color: $white;
    color: $black;
    font-weight: 300;
    line-height: 24px;
    font-size: 16px;
    width: 100%;
  }
  @include e(btn) {
    padding: 17px 30px 18px;
    background-color: $blackBrown;
    color: $white;
    font-family: $karla;
    width: 100%;
    line-height: 21px;
    font-size: 14px;
    text-transform: uppercase;
  }
}
