.flex-art {
  display: flex;
  gap: 30px;
  justify-content: space-around;
  align-items: center;
  padding-top: 0;
}
.art {
  margin-bottom: 0;
  gap: 10px;
  padding: 30px;
  flex-basis: 30%;
  position: relative;
  top: -55px;
  @include e(title) {
    flex-basis: 10%;
  }
  @include e(text) {
    flex-basis: 50%;
    margin: 0;
  }
}
.options {
  flex-basis: 60%;
  gap: 20px;
}
.quote {
  padding-bottom: 150px;
  @include e(main) {
    padding: 0 130px;
  }
}
