.contact-bg {
  background: url(../../../i/contact_bg.jpg), no-repeat center;

  background-size: cover;
  background-position: 20%;
  min-height: 400px;
  padding-bottom: 50px;
  // display: flex;
  // align-items: center;
  @include e(div) {
    position: relative;
    top: 170px;
  }
  @include e(text) {
    margin: 0;
    font-size: 24px;
    color: $white;
  }
}
.info {
  background-color: $backgroundColor;
  padding-top: 50px;
  @include e(contact) {
    font-family: $allura;
    color: $alluraColor;
    font-size: 40px;
    line-height: 40px;
    font-weight: 600;
    margin: 0;
  }
  @include e(title) {
    color: $blackBrown;
    font-size: 24px;
    margin-top: 0;
  }
  @include e(lorem) {
    font-family: $karla;

    line-height: 24px;
    color: $lightgrey;
    margin-bottom: 20px;
  }
  @include e(bg) {
    grid-column: 1;
    align-self: center;
    width: 60px;
    height: 60px;
    background-color: $alluraColor;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
  }
  @include e(icon) {
    width: 25px;
    height: 25px;
    fill: #f8ece3;
  }
  @include e(right) {
    grid-column: 2;
  }
  @include e(box) {
    display: grid;
    grid-template-columns: 60px 1fr;
    gap: 15px;
  }
  @include e(tit) {
    margin: 0;
    margin-bottom: 5px;
  }
  @include e(descr) {
    margin: 0;
    margin-bottom: 10px;
  }
  @include e(media) {
    &:before {
      content: "";
      display: block;
      background-color: #e7e0e0;
      width: 100%;
      height: 0.5px;
      margin-bottom: 10px;
    }
  }
  @include e(item) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    text-align: center;
    vertical-align: middle;
    background-color: $alluraColor;
  }
  @include e(icons-media) {
    display: flex;
    gap: 20px;
  }
  @include e(media-icon) {
    display: flex;
    align-items: center;
    align-content: center;
    width: 20px;
    height: 20px;
    fill: $white;
  }
  @include e(form) {
    border: 1px solid $lightgrey;
    padding: 10px;
    margin-bottom: 20px;
  }
  @include e(string) {
    padding: 10px;
    font-family: $karla;
    font-weight: 300;
    @include m(last) {
      padding-bottom: 50px;
      margin-bottom: 30px;
    }
  }
  @include e(form-box) {
    background-color: $white;
    padding: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  }
  @include e(left) {
    margin-bottom: 20px;
  }
  @include e(btn) {
    padding: 20px 32px;
    background-color: $blackBrown;
    color: $white;
    font-family: $karla;
    line-height: 21px;
    font-size: 14px;
    text-transform: uppercase;
  }
}
#map {
  height: 400px;
}
