.service {
  padding-bottom: 50px;
  @include e(party) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto 1fr;
    gap: 20px;
  }
  @include e(item) {
    margin-bottom: 0;
    padding: 30px 20px;
  }
  @include e(title-main) {
    font-size: 48px;
    line-height: 48px;
  }
}
.service-bg {
  @include e(text) {
    font-size: 48px;
  }
}
