html {
  font-size: cssvar(font-size);
}

body,
::after,
::before {
  font-size: 1rem;
  box-sizing: border-box;
  font-family: $baseFontFamily;
  font-size: $baseFontSize;
}

.container {
  margin: auto;
  padding: 0 12px;
}
.img-fluid {
  width: 100%;
  max-width: 100%;
  height: 100%;
  vertical-align: top;
  object-fit: cover;
}
.hide {
  display: none;
}
.pt {
  padding-top: 50px;
}
.form-val {
  border: 2px solid red;
}
