.info {
  @include e(grid) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
    gap: 30px;
  }
  @include e(left) {
    grid-column: 1;
  }
  @include e(right) {
    grid-column: 2;
  }
  @include e(item) {
    &:hover {
      background-color: $blackBrown;
    }
  }
  @include e(bg) {
    &:hover {
      background-color: $blackBrown;
    }
  }
  @include e(btn) {
    cursor: pointer;
    &:hover {
      color: $hoverColor;
    }
  }
}
#map {
  grid-column: 1 / -1;
  grid-row: 2;
}
