.favorite__description {
  padding: 0 25px;
}
.catalog {
  @include e(cake) {
    grid-template-rows: 667px 500px;
  }
  @include e(btn) {
    @include m(text) {
      &:hover {
        color: $hoverColor;
      }
    }
  }
}
.price {
  @include e(btn) {
    @include m(text) {
      &:hover {
        color: $hoverColor;
      }
    }
  }
}
