.check__li:hover .check__icon use {
  fill: #d0a270;
}
.about {
  padding-bottom: 50px;
  grid-template-columns: repeat(24, 1fr);
  grid-template-rows: 50px auto 50px;
  .background {
    display: block;
    grid-column: 1 / span 10;
    grid-row: 1 / span 3;
  }
  @include e(img) {
    @include m(first) {
      grid-column: 2 / span 3;
      grid-row: 2;
    }
    @include m(second) {
      grid-column: 5 / span 7;
      grid-row: 2;
    }
  }
  @include e(main) {
    grid-column: 13/24;
    grid-row: 2;
  }
}
.check {
  @include e(btn) {
    @include m(text) {
      &:hover {
        color: $hoverColor;
      }
    }
  }
}
