.main {
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-template-rows: auto 1fr;
  @include e(img) {
    grid-column: 1;
  }
  @include e(social) {
    grid-column: 2;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-self: center;
    justify-self: center;
  }
  @include e(title) {
    grid-column: 1;
    font-size: 70px;
    line-height: 84px;
  }
  @include e(text) {
    grid-column: 1;
  }
  @include e(btn) {
    grid-column: 1;
    width: 25%;
  }
}
.about-bg {
  @include e(text) {
    font-size: 48px;
  }
}
