.chef {
  background-color: $backgroundColor;
  padding-bottom: 40px;
  @include e(allura) {
    font-family: $allura;
    font-size: 40px;
    margin: 0;
    margin-bottom: 10px;
    color: $alluraColor;
    line-height: 40px;
  }
  @include e(title) {
    font-size: 24px;
    font-weight: 600;
    line-height: 24px;
    margin: 0;
    margin-bottom: 20px;
    color: $blackBrown;
  }
  @include e(lorem) {
    font-family: $karla;
    font-weight: 300;
    font-size: 16px;
    color: $lightgrey;
    margin: 0;
    margin-bottom: 30px;
  }
  @include e(btn) {
    line-height: 21px;
    font-family: $karla;
    text-transform: uppercase;
    padding: 20px 32px;
    background-color: $blackBrown;
    color: $backgroundColor;
    margin-bottom: 30px;
    display: inline-block;
    font-size: 15px;

    @include m(text) {
      text-decoration: none;
      color: $white;
    }
  }
  @include e(left) {
    margin-bottom: 30px;
  }
}
