.slider {
  background-color: $backgroundColor;

  @include e(profile) {
    width: 75px;
    height: 75px;
    border-radius: 50%;
  }
  @include e(stars) {
    margin-bottom: 25px;
  }
  @include e(lorem) {
    color: $lightgrey;
    font-family: $karla;
    font-size: 14px;
    line-height: 21px;
    font-weight: 300;
  }
  @include e(job) {
    grid-column: 2;
    grid-row: 2;
    color: $lightgrey;
    font-family: $karla;
    font-size: 14px;
    line-height: 21px;
    margin: 0;
  }
  @include e(name) {
    color: $blackBrown;
    font-size: 18px;
    line-height: 25px;
    grid-column: 2;
    grid-row: 1;
    margin: 0;
  }
  @include e(autor) {
    display: grid;
    gap: 10px;
    grid-template-columns: 75px 2fr;
    grid-template-rows: auto 1fr;
  }
  @include e(img-box) {
    grid-column: 1;
    grid-row: 1 / span 2;
  }
}
.swiper {
  background-color: $white;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);

  margin: 0 15px;
  position: relative;
  top: -100px;
}
.swiper-slide {
  padding: 15px 60px;
  width: 100%;
}
.icon-star {
  width: 15px;
  height: 15px;
  margin-right: 5px;
}
