.art {
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;

  padding: 50px;
  @include e(text) {
    padding: 0;
    align-content: center;
    margin-left: 25px;
    font-size: 28px;
    line-height: 28px;
  }
}
.flex-art {
  padding-top: 15px;
}
.options {
  display: flex;
}
.quote {
  padding-bottom: 50px;
  @include e(main) {
    font-size: 28px;
    padding: 0 35px;
    line-height: 54px;
  }
}
