.footer {
  background-color: $blackBrown;

  @include e(content) {
    padding: 40px 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 20px;
  }
  @include e(img) {
    width: 50%;
    margin: 0 auto;
  }
  @include e(menu) {
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
    list-style: none;
    display: flex;
    gap: 20px;
    justify-content: center;
  }
  @include e(link) {
    text-decoration: none;
    color: $white;
    font-family: $karla;
    font-weight: 300;
  }
  @include e(theme) {
    color: $white;

    font-family: $karla;
  }
}
