//fonts
$google-font: "https://fonts.googleapis.com/css2?family=Allura&family=Karla:wght@300&family=Lora:ital@0;1&display=swap";
$prefix: prefix- !default;
$baseFontSize: 16px;
$baseFontFamily: "Lora", sans-serif;
$karla: "Karla", sans-serif;
$allura: "Allura", cursive;
$mediumFontWeight: 500;

$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths

$imagesPath: "../i/";
$fontsPath: "../fonts/";
$usersFilesPath: "../userfiles/";

// Colors
$black: #000000;
$white: #ffffff;
$blue: #0d6efd !default;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;
$pink: #d63384 !default;
$red: #dc3545 !default;
$beige: #f8ece3 !default;
$backgroundColor: #fcfcf4 !default;
$alluraColor: #d0a270 !default;
$blackBrown: #412000 !default;
$lightgrey: #7a7a7a !default;
$darkgreen: #074838 !default;
$hoverColor: #9b7a26 !default;

$cssvars: (
  "font-family": $baseFontFamily,
  "font-size": $baseFontSize,
  "black": $black,
  "white": $white,
  "blue": $blue,
  "indigo": $indigo,
  "purple": $purple,
  "pink": $pink,
  "red": $red,
) !default;
