.footer {
  @include e(content) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
  }
  @include e(img) {
    width: 200px;
    margin: 0;
  }
  @include e(theme) {
    align-self: center;
  }
  @include e(menu) {
    margin: 0;
  }
  @include e(link) {
    &:hover {
      color: rgb(155, 122, 38);
    }
  }
}
