.event {
  background-color: $backgroundColor;
  @include e(post) {
    height: 540px;
    background: url(../../../i/chocolate_cake.jpg), no-repeat center;
    background-size: 100% 540px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  @include e(every) {
    margin: 0;
    font-size: 40px;
    font-family: $allura;
    color: $alluraColor;
  }
  @include e(title-main) {
    color: $white;
    font-size: 24px;
    font-weight: 600;
    line-height: 24px;
    margin: 0;
    margin-bottom: 20px;
  }
  @include e(title) {
    font-size: 24px;
    margin: 0;
    color: $alluraColor;
    margin-bottom: 16px;
  }
  @include e(lorem) {
    color: $white;
    font-weight: 300;
    margin: 0;
    padding: 0 20px;
    font-family: $karla;
    line-height: 24px;
    margin-bottom: 15px;
  }
  @include e(party) {
    margin: 0 10px;
  }
  @include e(item) {
    background-color: $darkgreen;
    padding: 30px 50px;
    display: flex;
    text-align: center;
    position: relative;
    top: -100px;
    margin-bottom: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
  @include e(btn) {
    @include m(text) {
      color: $white;
      font-family: $karla;
      font-size: 16px;
      text-decoration: none;
    }
    &::after {
      content: "\2192";
      width: 10px;
      height: 10px;
      color: $white;
      padding-left: 10px;
    }
  }
}
.icon-event {
  fill: $alluraColor;
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
}
