.main__media:hover .media-icon use {
  fill: $blackBrown;
}
.main {
  display: grid;
  grid-template-columns: 25px 1fr 1fr 1fr 30px 25px;
  grid-template-rows: auto 1fr;
  @include e(img) {
    grid-column: 3 / span 2;
    grid-row: 1 / span 3;
  }
  @include e(social) {
    grid-column: 5;
    grid-row: 1;
    align-self: end;
  }
  @include e(title) {
    grid-column: 2 / span 2;
    grid-row: 1;
  }
  @include e(text) {
    grid-column: 2;
    grid-row: 2;
  }
  @include e(btn) {
    grid-column: 2;
    grid-row: 3;
    width: 50%;
    @include m(text) {
      &:hover {
        color: $hoverColor;
      }
    }
  }
  @include e(media) {
    cursor: pointer;
    &:hover {
      color: $alluraColor;
    }
  }
}
.navbar {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.logo img {
  max-width: 55%;
}
