.menues {
  background-color: $backgroundColor;
  padding-bottom: 30px;
}
.price {
  @include e(title) {
    margin: 0;
    font-size: 24px;
    color: $blackBrown;
  }
  @include e(item) {
    margin-bottom: 40px;
    &:first-of-type {
      margin-bottom: 50px;
    }
    &:last-of-type {
      margin-bottom: 0;
      padding-bottom: 50px;
    }
  }
  @include e(name) {
    margin: 0;
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
    color: $alluraColor;
  }
  @include e(line) {
    height: 1px;
    background-color: $black;
    width: 100%;
    margin: 15px 0;
  }
  @include e(lorem) {
    font-family: $karla;
    color: $lightgrey;
    line-height: 24px;
    font-weight: 300;
  }
  @include e(btn) {
    line-height: 21px;
    font-family: $karla;
    text-transform: uppercase;
    padding: 20px 32px;
    background-color: $blackBrown;
    color: $backgroundColor;

    font-size: 15px;
    @include m(text) {
      text-decoration: none;
      color: $backgroundColor;
    }
  }
  @include e(descr) {
    line-height: 16px;
    font-family: $karla;
    font-weight: 300;
    color: $lightgrey;
    margin: 0;
  }
}
