.page404 {
  background: url(../../../i/404.jpg);
  background-size: cover;
  background-color: $black;
  background-position: center center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  align-items: center;
  @include e(title) {
    color: $white;
    font-family: $karla;
    font-size: 110px;
    line-height: 88px;
    font-weight: 600;
    margin: 0;
  }
  @include e(secondTitle) {
    color: $white;
    font-size: 24px;
    line-height: 24px;
    font-weight: 600;
    margin: 0;
  }
  @include e(descr) {
    color: $white;
    font-family: $karla;
    line-height: 24px;
    text-align: center;
    padding: 0 40px;
  }
  @include e(btn) {
    text-transform: uppercase;
    padding: 20px 40px;
    background-color: $alluraColor;

    @include m(text) {
      text-decoration: none;
      color: $backgroundColor;
    }
  }
}
