.container_main {
  background-color: $backgroundColor;
}
.flex-art {
  padding-top: 30px;
}
.art {
  margin-bottom: 100px;
  padding: 15px;

  background-color: #214a38;
  color: $white;
  @include e(title) {
    font-size: 58px;
    margin: 0;
  }
  @include e(text) {
    font-size: 24px;
    margin: 0;
    padding-bottom: 20px;
    font-weight: 600;
  }
  @include e(lorem) {
    font-family: $karla;
    margin: 0;
  }
}
.options {
  display: flex;
  display: none;
  .post {
    @include e(title) {
      font-size: 18px;
      font-weight: 600;
      margin: 0;
      color: $blackBrown;
      @include m(truck) {
        font-size: 20px;
        padding-right: 10px;
      }
    }
    @include e(lorem) {
      font-family: $karla;
      padding: 0 25px;
      color: $lightgrey;
    }
  }
}
.quote {
  padding-bottom: 35px;
  @include e(main) {
    z-index: 100;
    font-size: 22px;
    font-style: italic;
    font-weight: 600;
    margin: 0;
    letter-spacing: 1px;
    line-height: 42px;
    text-align: center;
    color: $blackBrown;
  }
  @include e(line) {
    text-align: center;
    font-size: 25px;
    font-weight: 600;
  }
  @include e(autor) {
    text-align: center;
    font-family: $karla;
    font-size: 18px;
    text-transform: uppercase;
    color: #d0a270;
    font-weight: 600;
    margin: 0;
  }
}
.main {
  @include e(social) {
    display: none;
  }
}
.icon-left {
  width: 30px;
  height: 30px;
  position: relative;
  top: 7px;
  right: 8px;
  fill: $blackBrown;
}
