.background {
  display: none;
  background-color: $alluraColor;
}

.about {
  background-color: $backgroundColor;
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto 1fr;

  @include e(bg) {
    background-color: $backgroundColor;
  }
  @include e(img) {
    @include m(first) {
      grid-column: 1;
      grid-row: 1 / span 2;
    }
    @include m(second) {
      grid-column: 2 / span 2;
      grid-row: 1 / span 2;
    }
  }
  @include e(main) {
    margin-top: 30px;

    grid-column: 1 / span 3;
    grid-row: 3;
  }
  @include e(title) {
    margin: 0;
    font-size: 24px;
    color: $blackBrown;
    font-weight: 600;
  }
  @include e(lorem) {
    font-family: $karla;
    color: $lightgrey;
    margin-bottom: 30px;
    line-height: 1.5;
    padding-right: 5px;
  }
  @include e(us) {
    font-family: $allura;
    color: $alluraColor;
    font-size: 40px;
    margin: 0;
    align-self: start;
  }
}

.check {
  padding: 0 10px;
  margin: 0;
  list-style: none;
  @include e(li) {
    color: #034337;
    margin-bottom: 10px;
    font-family: $karla;
    @include m(wedding) {
      margin-bottom: 50px;
    }
  }
  @include e(icon) {
    display: inline-block;
    position: relative;
    top: 1px;
    width: 15px;
    height: 15px;
    margin-right: 15px;
  }
  @include e(btn) {
    text-transform: uppercase;
    padding: 20px 32px;
    background-color: $blackBrown;
    color: $backgroundColor;
    margin-bottom: 80px;
    @include m(text) {
      text-decoration: none;
      color: $backgroundColor;
    }
  }
}
