.slider {
  padding-bottom: 50px;
  @include e(main) {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: 30px auto 30px;
  }
  @include e(img) {
    grid-column: 6 / -1;
    grid-row: 2;
  }
  @include e(swiper) {
    grid-column: 1 / 7;
    grid-row: 1 / 4;
  }
  @include e(lorem) {
    padding-top: 50px;
  }
}
.swiper {
  position: initial;
  //   min-height: 400px;
}
.swiper-slide {
  padding: 10px 40px;
}
