.form {
  @include e(content) {
    padding: 50px;
    // display: grid;
    // grid-template-columns: 2fr 1fr;
  }
  @include e(group) {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 25px;
  }
  @include e(title) {
    text-align: center;
    font-size: 48px;

    line-height: 48px;
  }
  @include e(place) {
    margin-bottom: 0;
    grid-column: 1;
    max-width: 555px;
  }
  @include e(btn) {
    grid-column: 2;
    align-self: center;
    width: 180px;
  }
}
