.favorite {
  @include e(title) {
    font-size: 48px;
  }
  @include e(lorem) {
    padding: 0 100px;
  }
}
.buttons {
  justify-content: center;
}
.catalog {
  @include e(cake) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
    gap: 30px;
  }
  .btn-container {
    grid-column: 1 / span 2;
    grid-row: 2;
    position: relative;
    top: 330px;
  }

  @include e(item) {
    @include m(first) {
      grid-column: 1;
      grid-row: 1;
    }
    @include m(second) {
      grid-column: 1;
      grid-row: 2;
      position: relative;
      top: -159px;
    }
    @include m(third) {
      grid-column: 2;
      grid-row: 1;
    }
    @include m(fourth) {
      grid-column: 2;
      grid-row: 2;
    }
  }
}
