.gallery-bg {
  background: url(../../../i/gallery_bg.jpg), no-repeat center;

  background-size: cover;
  background-position: 20%;
  min-height: 400px;

  // display: flex;
  // align-items: center;
  @include e(div) {
    position: relative;
    top: 170px;
  }
  @include e(text) {
    margin: 0;
    font-size: 24px;
    color: $white;
  }
}
.swiper2 {
  padding: 0;
  padding-bottom: 50px;
  height: 300px;

  @include e(pagination) {
    position: absolute;
    top: 0;
    right: 10px;
    color: $blackBrown;
    margin: 0;
    padding: 10px;
    background-color: $backgroundColor;
  }
}
.block {
  text-align: center;
  @include e(title) {
    font-size: 40px;
    font-family: $allura;
    color: $alluraColor;
    margin: 0;
    padding-top: 50px;
    margin-bottom: 10px;
  }
  @include e(tit) {
    font-size: 24px;
    color: $blackBrown;
    margin: 0;
  }
  @include e(lorem) {
    font-family: $karla;
    color: $lightgrey;
    margin-bottom: 35px;
    padding: 0 25px;
  }
}
.follow {
  padding-bottom: 50px;
  background-color: $backgroundColor;
  @include e(post) {
    padding-bottom: 35px;
  }
  @include e(title) {
    font-family: $allura;
    color: $alluraColor;
    margin: 0;
    font-size: 40px;
    line-height: 40px;
  }
  @include e(subtitle) {
    color: $blackBrown;
    font-size: 24px;
    font-weight: 600;
  }
  @include e(lorem) {
    font-family: $karla;
    font-weight: 300;
    color: $lightgrey;
    line-height: 24px;
  }
  @include e(imgs) {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(10, 1fr);
    max-height: 485px;
  }
  @include e(img) {
    @include m(first) {
      grid-column: 1 / span 5;
      grid-row: 1 / span 8;
    }
    @include m(second) {
      display: none;
    }
    @include m(third) {
      grid-column: 4 / -1;
      grid-row: 4 / -1;
    }
  }
}
