header {
  background-color: transparent;
  background-image: radial-gradient(at bottom left, #96693d 0%, #d4a474 100%);
  padding-bottom: 35px;
}
.header_about {
  background-color: $backgroundColor;
}
.about-bg {
  background: url(../../../i/about_bg.jpg), no-repeat center;
  background-size: cover;
  background-position: 20%;
  min-height: 400px;
  // display: flex;
  // align-items: center;
  @include e(div) {
    position: relative;
    top: 170px;
  }
  @include e(text) {
    margin: 0;

    font-size: 24px;
    color: $white;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
  font-family: $karla;
  font-size: 14px;
  text-transform: uppercase;
}

.navbar a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.6);
  transition: color 200ms ease-in;
}

.navbar li:hover > a {
  color: rgba(0, 0, 0, 0.85);
}

.logo img {
  /* Get rid of the bottom whitespace present on inline elements. */
  display: block;
  min-width: 100px;
  max-width: 25%;
}
.menu-button {
  transition: 0.3s ease-in-out;
  width: 35px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 2rem;
  right: 1rem;
}
.mobile-menu.visible .menu-button.in-mobile {
  position: absolute;
  top: 2rem;
  right: 1rem;
}
.menu-button_about {
  background-color: $blackBrown;
}
.menu-button span {
  position: absolute;
  height: 4px;
  border-radius: 10px;
  width: 100%;
  background-color: $white;
  top: 0;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}

.menu-button span:nth-child(1) {
  top: 0;
}

.menu-button span:nth-child(2),
.menu-button span:nth-child(3) {
  top: 10px;
}

.menu-button span:nth-child(4) {
  top: 20px;
}

.mobile-menu.visible .menu-button span:nth-child(1),
.mobile-menu.visible .menu-button span:nth-child(4) {
  opacity: 0;
}

.mobile-menu.visible .menu-button span:nth-child(2) {
  transform: rotate(45deg);
}

.mobile-menu.visible .menu-button span:nth-child(3) {
  transform: rotate(-45deg);
}

/*#endregion*/

/** ---------------------------------------------------------------------------
 * Mobile menu 
 * ------------------------------------------------------------------------- */

/*#region*/

/* Dark overlay.  */
.dark-overlay {
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  z-index: 99998;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}

.dark-overlay.visible {
  opacity: 1;
  visibility: visible;
}

/* Mobile menu. */
.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobile-menu {
  background-color: $blackBrown;
  padding: 6rem 0 2rem;
  position: fixed;
  width: 20rem;
  top: 0;
  right: -100%;
  transition: right 0.3s;
  z-index: 99999;
  height: 100%;
  overflow: scroll;
}

.mobile-menu.visible {
  left: 0;
}

.mobile-menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.mobile-menu .menu-wrap a {
  display: block;
  padding: 1rem;
  color: $white;
  text-decoration: none;
  transition: all 0.3s;
}

.mobile-menu .menu-wrap a.active {
  background-color: rgba(0, 0, 0, 0.1);
}

.mobile-menu .menu-wrap li {
  position: relative;
}

/* 1st level. */
.mobile-menu .menu-wrap > ul > li > a {
  font-weight: bold;
}

/* Lower levels. */
.mobile-menu .menu-wrap li ul {
  padding-left: 2rem;
  display: none;
}

/* Drop-down arrows. */
.mobile-menu .menu-wrap li.has-submenu:after {
  content: "";
  position: absolute;
  top: 1.6rem;
  width: 0;
  height: 0;
}

.mobile-menu .menu-wrap li.has-submenu:after {
  right: 1rem;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid rgb(255, 255, 255);
}

.mobile-menu .menu-wrap li.has-submenu.opened:after {
  border-bottom: 5px solid rgb(255, 255, 255);
  border-top: none;
}
@media screen and (min-width: 992px) {
  /*#region*/
  .dark-overlay {
    display: none;
  }
  .menu-button {
    display: none;
  }
  .menu li {
    position: relative;
  }

  /* 1st level. */
  .menu > ul > li {
    display: inline-block;
  }

  .menu > ul > li > a {
    display: inline-block;
    padding: 1rem 1.5rem;
    color: $blackBrown;
    font-size: 16px;
    line-height: 21px;
    font-weight: 600;
    &:hover {
      color: $white;
    }
  }

  /* 2nd level. */
  .menu li ul {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    left: 0;
    width: 15rem;
    padding: 1.5rem 0;
    background: $white;
    transition: all 300ms ease;
    z-index: 10000;
    border-radius: 5px;
  }
  .menu li ul a {
    color: $black;
    text-transform: initial;
    &:hover {
      color: $alluraColor;
    }
  }

  .menu li:hover > ul {
    opacity: 1;
    visibility: visible;
  }

  .menu li ul a {
    display: block;
    padding: 0.5rem 1.5rem;
  }

  /* 3rd level. */
  .menu li ul li ul {
    top: -1.5rem;
    left: 100%;
    z-index: 9000;
  }

  /* Drop-down arrows. */
  .menu > ul > li.has-submenu:after,
  .menu li ul li.has-submenu:after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 0;
    height: 0;
  }

  /* 1st level arrows. */
  .menu > ul > li.has-submenu:after {
    right: 5px;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid rgba(0, 0, 0, 0.25);
  }

  /* 2nd level arrows. */
  .menu li ul li.has-submenu:after {
    right: 1.5rem;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid rgba(0, 0, 0, 0.25);
  }

  /*#endregion*/
}

.main {
  padding-bottom: 50px;
  @include e(title) {
    font-size: 29px;
    line-height: 30px;
    color: $beige;
    font-weight: 300;
    margin-bottom: 20px;
  }
  @include e(social) {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  @include e(text) {
    font-family: $karla;
    color: $white;
    margin-top: 0;
    line-height: 24px;
  }
  @include e(btn) {
    background-color: $blackBrown;
    padding: 20px 32px;
    @include m(text) {
      color: $beige;
      text-transform: uppercase;
      text-decoration: none;
    }
  }
}
.media-icon {
  width: 20px;
  height: 20px;
  fill: #f8ece3;
}
