$slickFonts: (
  "slick.eot": "embedded-opentype",
  "slick.woff": "woff",
  "slick.ttf": "truetype",
  "#slick": "svg",
);

@font-face {
  font-family: "icomoon";
  src: url("../../fonts/icomoon.eot?7mkoot");
  src: url("../../fonts/icomoon.eot?7mkoot#iefix") format("embedded-opentype"),
    url("../../fonts/icomoon.ttf?7mkoot") format("truetype"),
    url("../../fonts/icomoon.woff?7mkoot") format("woff"),
    url("../../fonts/icomoon.svg?7mkoot#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;

  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-bestseller::before {
  content: "\e900";
}
.icon-heart::before {
  content: "\e901";
}
.icon-truck::before {
  content: "\e903";
}
.icon-facebook::before {
  content: "\e904";
}
.icon-instagram::before {
  content: "\e905";
}
.icon-twitter::before {
  content: "\e906";
}
.icon-linkedin2::before {
  content: "\e907";
}
.icon-check::before {
  content: "\f058";
  color: #412000;
  font-size: 24px;
  :hover {
    color: #d4a474;
    transition: all 0.3;
  }
}
