.gallery-bg {
  @include e(text) {
    font-size: 48px;
  }
}
.swiper2 {
  height: 500px;
  @include e(pagination) {
    font-size: 28px;
    line-height: 28px;
    right: 32px;
  }
}
.block {
  @include e(tit) {
    font-size: 48px;
  }
}
.follow {
  @include e(grid) {
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(10, 1fr);
  }
  @include e(subtitle) {
    font-size: 48px;
    line-height: 48px;
  }
  @include e(img) {
    background-size: cover;
    @include m(second) {
      display: initial;
      grid-column: 8/ 20;
      grid-row: 1/ 6;
      z-index: 1;
    }
    @include m(first) {
      grid-row: 2 / span 8;
      grid-column: 1 / span 16;
      z-index: 2;
    }
    @include m(third) {
      z-index: 3;
      grid-column: 10 / -1;
      grid-row: 6 / -1;
    }
  }

  @include e(post) {
    grid-column: 1 / 6;
    padding: 0;
  }
  @include e(imgs) {
    grid-column: 6 / -1;
    grid-template-columns: repeat(20, 1fr);
  }
}
